import React from 'react'


const Person = ({persons,filterIn, deleteUser}) => {
    
    return (
    persons.filter(person => person.name.toLowerCase().includes(filterIn.toLowerCase()))
    .map((person,i) => 
    <li key={person.name}> {person.name} - {person.number}
    <button onClick={() => deleteUser(person)}>Delete</button>
    </li>
    )
    )   
}

export default Person