import React, { useState, useEffect } from 'react'
import Filter from './Components/Filter'
import PersonForm from './Components/PersonForm'
import Persons from './Components/Persons'
import contacts from './Services/Contacts'
import Notification from './Components/Notification'

const App = () => {

  const [persons, setPersons] = useState([])
  const [newName, setNewName ] = useState('')
  const [newNumber, setNewNumber] = useState('')
  const [newFilter, setFilter] = useState('')
  const [message, setMessage] = useState(null)
  const [errorMessage, setErrorMessage] = useState(null)

  useEffect(() => {
    
    contacts
      .getAll()
      .then(allNotes => {
        setPersons(allNotes)
      })
  }, [])

  const handleNameChange = (event) => {
    setNewName(event.target.value)
  }

  const handleNumChange = (event) => {
    setNewNumber(event.target.value)
  }

  const handleFilter = (event) => {
    setFilter(event.target.value)
  }

  const addContact = (event) => {
    event.preventDefault()

    const nameObject = {
      name: newName,
      number : newNumber
      
    }
   
    const doubleNameLow = persons.some(person => person.name.toLowerCase() === newName.toLowerCase())
    //const doubleNum = persons.some(person => person.number === newNumber)

    if (doubleNameLow) {
      const person = persons.find(p => p.name === newName)
      const changedPerson = { ...person, number: newNumber }
      const { id } = person
      
      
      if (window.confirm(`Overwrite number for:  ${newName}?`)) {
      contacts
      .update(id, changedPerson)
      .then(() => {
        setPersons(persons.map(person => person.id !== id ? person : changedPerson))
        setMessage(`Overwrote: ${newName}`)
        setTimeout(() => {
          setMessage(null)
        }, 3000)
      })
      .catch(error => {
        setErrorMessage(error.response.data.error.message)
        setTimeout(() => {
          setErrorMessage(null)
        }, 3000)
        })
    }
  }

      else {
      
      contacts
      .create(nameObject)
      .then(createData => {
        setPersons(persons.concat(createData))
        setMessage(`Added: ${newName}`)
        setTimeout(() => {
          setMessage(null)
        }, 3000)
      })
      .catch(error => {
        setErrorMessage(error.response.data.error.message)
        setTimeout(() => {
          setErrorMessage(null)
        }, 3000)
        
      })
 
  }
  setNewName('')
  setNewNumber('')
}


  const deleteUser = (props) => {
    
    if (window.confirm(`Delete user ${props.name}?`)) {
    contacts
    .deleteUser(props.id)
    .then(()=> {
      const filtState = persons.filter(n => n.id !== props.id)
      setPersons(filtState)
      setMessage(`Deleted: ${props.name}`)
      setTimeout(() => {
        setMessage(null)
      }, 3000)
    })
    .catch(error => {
      setErrorMessage(`Error updating: ${newName}`)
      setTimeout(() => {
        setErrorMessage(null)
      }, 3000)
      })
  }
  }

  return (
    <div>
      <h1>Phonebook</h1>
      <Filter value={newFilter} onChange={handleFilter}/>
      <h2>Add a new contact: </h2>
      <PersonForm onSubmit={addContact} onChangeName={handleNameChange} onChangeNum={handleNumChange} valueName={newName} valueNumber={newNumber}/>
      <Notification message={message} errorMessage={errorMessage}/>
      <h2>Names - numbers</h2>
        <ol>
        <Persons persons={persons} filterIn={newFilter} deleteUser={deleteUser}/>
        </ol>
    </div>
  )
}

export default App;